declare var $: any
import { Component } from '@angular/core';
import { AndroidPermissions } from '@ionic-native/android-permissions';
import { Camera, CameraOptions } from '@ionic-native/camera';
import { File } from '@ionic-native/file';
import { FileOpener } from '@ionic-native/file-opener';
import { FileTransfer } from '@ionic-native/file-transfer';
import { ActionSheetController, Events, IonicPage, LoadingController, MenuController, NavController, NavParams, Platform, ToastController } from 'ionic-angular';
import { ConfigProvider } from '../../../providers/config';
import { HttpServicesProvider } from '../../../providers/http-services';
import { PhoneDeviceProvider } from '../../../providers/phone-device';
import { StorageProvider } from '../../../providers/storage';
import { Utils } from '../../../providers/Utils';
declare var fuzzySearch:any 
/**
 * Generated class for the LmtagstartupreportPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-lmtagstartupreport',
  templateUrl: 'lmtagstartupreport.html',
})
export class LmtagstartupreportPage {

  public list = [] // 列表
  // public lmtagStatusList = []
  public projectid = ''
  // public filepath = '';
  // public currentTab = 0
  public search = ''
  // public status = 1
  public timer = null
  // public archiveid = null
  public report = {}
  public editItemFlag = false;

  constructor(public navCtrl: NavController,
    public navParams: NavParams,
    public httpService: HttpServicesProvider,
    public phoneDevice: PhoneDeviceProvider,
    public platform: Platform,
    public toastCtrl: ToastController,
    public permission: AndroidPermissions,
    public file: File,
    public camera: Camera,
    public transfer: FileTransfer,
    public fileopen: FileOpener,
    public loadingCtrl: LoadingController,
    public configProvider: ConfigProvider,
    public menuCtrl: MenuController,
    public events: Events,
    public actionsheetCtrl: ActionSheetController,
    public storage: StorageProvider) {
    this.phoneDevice.phoneBacktoPop(this.navCtrl);
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad LmtagarchivemanagePage');
  }
  ionViewWillEnter() { //当将要进入页面时触发
    // this.list = [];
    // this.lmtagStatusList = [];
    // this.initproject();
    this.projectid = this.storage.get(StorageProvider.LM_PROJECTID);
    // this.getDict();
    if (this.projectid) {
      this.initlist();
      // this.initTree();
    }
  }

  doRefresh(refresher) {
    // this.initproject()
    // this.getDict();
    this.initlist();
    setTimeout(() => {
      refresher.complete(); //停止下拉刷新
    }, 1500)
  }

  // changeTab(status, i) {
  //   this.currentTab = i
  //   this.status = status
  //   this.initlist()
  // }

  // 搜索+防抖
  handleSearch() {
    // console.log(this.search);
    clearTimeout(this.timer)
    this.timer = setTimeout(() => {
      this.initlist()
      this.timer = null
    }, 300)
  }

  handleInfo(item, edit = false) {
    if (edit) {
      this.events.subscribe('saveListCall', (params) => {
        // console.log(params);
        const outData = JSON.parse(params)
        // 接收B页面发布的数据
        console.log(outData);
        this.report = outData
        this.saveReport()
        // 取消订阅
        this.events.unsubscribe('saveListCall');
      })
    }
    this.navCtrl.push('LmtagstartupreportinfoPage', { item, edit })
  }

  initlist() {
    this.list = []
    let url = `/lmtagStartupReport/list?sort=createtime&order=desc&projectid=${this.projectid}&_search=false&rows=50&page=1&sidx=&sord=asc&fuzzysearch=${this.search}`
    this.httpService.get(url, null, (result) => {
      if (result != null) {
        this.list = result.rows
      }
    });
  }

  cancelEidt() {
    this.editItemFlag = false
    this.report['version'] && this.initlist()
    this.report = {}
  }

  // 编辑
  editTraining(item, index) {
    // 已经有 编辑/新增项 在操作
    if (this.editItemFlag) {
      return this.toastCtrl.create({
        position: 'top',
        message: '有操作暂未保存',
        duration: 1200
      }).present();
    }
    this.report = {
      ...item,
      uploadtime: Utils.dateFormat(new Date(), 'yyyy-MM-dd hh:mm:ss')
    }
    this.list.splice(index, 1)
    this.editItemFlag = true
  }

  // 新建
  createReport() {
    const userinfo = this.storage.get(StorageProvider.ME_USERINFO);
    this.report = {
      editor: userinfo.id,
      editorname: userinfo.name,
      region: '', // 说明
      uploadtime: Utils.dateFormat(new Date(), 'yyyy-MM-dd hh:mm:ss'),
      phone: userinfo.phone || '',
      projectid: this.projectid,
      uploader: userinfo.id,
      uploadername: userinfo.name,
      reportid: Utils.getUuid()
    }
    // this.editItemFlag = true
    this.photo('lmtagStartupReport', false) // 拉起
  }

  saveReport(showTip = false) {
    console.log(this.report);
    const data = [this.report, ...this.list]
    this.httpService.post('/lmtag/lmtagStartupReportService/saveList', data, res => {
      if (res.code == 0) {
        if (showTip) {
          this.toastCtrl.create({
            position: 'top',
            duration: 1200,
            message: '保存成功',
          }).present();
        }
        this.editItemFlag = false
        // this.report = {}
        this.initlist()
      }
    })
  }

  removeReport(ids) {
    this.httpService.removeTableIds('lmtagStartupReport', { ids }, res => {
      console.log(res);
      if (res.code == 0) {
        this.toastCtrl.create({
          message:'删除成功',
          duration:1200,
          position:'top'
        }).present()
        this.initlist();
      }
    }, this.httpService.CONTENTTYPE_FORMURLENCODE)
  }

  /**
   * 选择人员
   */
  chooseperson(id, name) {
    this.navCtrl.push('UserdialogPage', {
      typeid: id,
      type: 'single',//单选
      typename: name
    });
    this.events.subscribe('user:userinfo', (data, time) => {
      console.log(data);

      this.report[data.typename] = data.name;
      this.report[data.typeid] = data.id;
      this.report['phone'] = data.phone
      this.events.unsubscribe('user:userinfo');
    })
  }

  beforeUpload(e) {
    const file = e.target.files[0]
    console.log(e.target.files);
    if (file.length == 0) {
      return
    }
    let formdata = new FormData()
    formdata.append('file', file)
    formdata.append('busstype', 'lmtagStartupReport')
    formdata.append('bussid', this.report['reportid'])
    let loading = this.loadingCtrl.create({
      content: '上传中，请稍等...',//数据加载中显示
      enableBackdropDismiss: true,
    });
    loading.present();
    $('#file')[0].value = ''
    this.httpService.postFormData('/uploadify/upload', formdata, res => {
      loading.dismiss();
      if (res.error == 0) {
        this.handleInfo(this.report, true) // 携带初始化数据进入详情
        return
      }
    })
  }
  photo(busstype,video) {
    const actionsheet = this.actionsheetCtrl.create({
      // cssClass:'headChoice',
      buttons: [
        {
          text: '拍照',
          role: 'destructive',
          cssClass: 'normal',
          handler: () => {
            this.doCamera(this.camera.PictureSourceType.CAMERA,busstype,video);
          }
        }, {
          text: '从手机相册中选择',
          cssClass: 'normal',
          handler: () => {
            this.doCamera(this.camera.PictureSourceType.PHOTOLIBRARY,busstype,video);
          }
        }, {
          text: '从文件中选择',
          cssClass: 'normal',
          handler: () => {
            $('#file').click();
          }
        },
          {
          text: '取消',
          role: 'cancel',
          cssClass: 'cancel',
          handler: () => {
          }
        }
      ]
    })
    actionsheet.present();
  }
  doCamera(sourceType,busstype,video) {
  
    let mediatype =0;
    if(video){
      mediatype =1;
    }
    const options: CameraOptions = {
      quality: 100, //图片质量
      destinationType: this.camera.DestinationType.FILE_URI,  /*返回的类型*/
      encodingType: this.camera.EncodingType.JPEG,
      sourceType: sourceType,
      mediaType: mediatype,////可选媒体类型：圖片=0，只允许选择图片將返回指定
      correctOrientation: true,//陀螺仪纠正照片方向
      // mediaType: this.camera.MediaType.PICTURE,
      //allowEdit:true,
      targetWidth: 1024,  /*宽度高度要设置*/
      targetHeight: 1024
    }
    // let that = this;
    let obj={};

    // 获取图片
    this.camera.getPicture(options).then((imageData) => {
      // 获取成功
      this.httpService.uploadify(busstype, this.report['reportid'], imageData, (result) => {
        
        this.handleInfo(this.report, true) // 携带初始化数据进入详情
      });
    })
  }

  initPicture(busstype, bussid) {//初始化图片/视频
    // this.imagesArray = [];
    this.httpService.getFiles(busstype, bussid, (result) => {
      if (result != null && result != undefined) {
        let datafiles = result.data;
        let index = 0;
        // this.files = [];
        console.log(datafiles);
      }
    });
  }

}


